import React from 'react'
import {Link } from "react-router-dom";

function Landing() {
    return (
        <div className="landing_container">
            <div className="enterArea">
            <h1 id='landingText'>MARVEL DATABASE</h1>
            </div>
            <div className='quoteArea'>
            <h2>“Face front, true believers!” - Stan Lee</h2>
            </div>
            {/* <li><Link to="/Main">React</Link></li> */}
            <div className='enterBTNDiv'>
            <Link to="/Main"><button>Enter</button>
            </Link>
            </div>
        </div>
    )
}

export default Landing