import React from 'react'

const Card = ({result, addActive}) => {
    return (
        <div id={result.id} key={result.id} className='card' onClick={(e) => addActive(e)} >
            <div className='mainBorder'>
            <h3>{result.name}</h3></div>
            <div className='side1'>
                <h3>Character Description</h3>
                <p>{result.description === '' ? 'Marvel API contained no information' : result.description}</p><a href={`https://marvel.fandom.com/wiki/Special:Search?query=${result.name}&scope=internal&navigationSearch=true`} target="_blank" rel="noopener noreferrer">Find more information</a>
                </div>
                <div className='side2'>
                    <img alt='character' src={result.thumbnail.path+'.jpg'}></img>
                    {/* <h4 ref={testElement}>{item.id}</h4> */}
                    </div>
            </div>
    )
}

export default Card
