import React from 'react'
import Search from './Search'
import CardList from './CardList'
import {useEffect, useState} from 'react'

const Main = () => {
    const [inputText, setInputText] = useState('')
    const [searchResults, setSearchResults] = useState([])
    const [resultStatus, setResultStatus] = useState(false)
    const [validEntry, setValidEntry] = useState(true)
    const [step, setStep] = useState(1)
    const [main, setMain] = useState(true)


    const searchAPI = (async () => {
            console.log('searching')
            const response = await fetch(`https://gateway.marvel.com/v1/public/characters?nameStartsWith=${inputText}&ts=123&apikey=b10a47de745dba309eba50894e1a9841&hash=f982e55609d27090f55b37d0682ce716`)
            const data = await response.json();
            setSearchResults(data.data.results)
            setValidEntry(true)
            setMain(false)
        })

    const formValidation = ()=> {
        let isValid = true;

        if(inputText.length === 0){
            isValid = false
            setValidEntry(false)
        }

        return isValid
    }

    const onSubmit = (e) => {
        e.preventDefault()
        const isValid = formValidation()
        if(isValid){
            searchAPI()
            console.log('valid input')
        } else {
            console.log('not valid')
        }
    }

    useEffect(() => {
        if(searchResults.length === 0 && main === true){
            // setResultStatus(false)
            setStep(1)
        } else if (searchResults.length === 0 && main === false) {
            // setResultStatus(true)
            setStep(3)
        } else {
            setStep(2)
        }
    }, [searchResults, main])

        function addActive(e){
            if(e.currentTarget.className == 'card'){
                e.currentTarget.className = 'card active';
                e.currentTarget.childNodes[0].className = 'mainBorder cardbkg active'
                e.currentTarget.childNodes[1].className = 'side1 cardbkg active'
                e.currentTarget.childNodes[2].className = 'side2 cardbkg active'
            } else if ( e.currentTarget.className == 'card active') {
                e.currentTarget.className = 'card'
                e.currentTarget.childNodes[0].className = 'mainBorder'
                e.currentTarget.childNodes[1].className = 'side1'
                e.currentTarget.childNodes[2].className = 'side2'
            }
        }

return (
<div>
    <Search onSubmit={onSubmit} setInputText={setInputText} inputText={inputText} searchAPI={searchAPI} validEntry={validEntry}/>
    {/* {resultStatus ? <CardList searchResults={searchResults} addActive={addActive}/> : <h1>No results found</h1>} */}
    {step === 1 ?<div className='textDiv'><h1>Enter a Marvel Character</h1></div> : step === 2 ? <CardList searchResults={searchResults} addActive={addActive}/> : step === 3 ?<div className='textDiv'> <h1>No results found</h1></div> : null}
</div>
    )
}

export default Main
